import { TrainerComponent } from './modules/trainer/trainer.component';
import { ViewProfileComponent } from './modules/trainer/view-profile/view-profile.component';
import { UpdateProfileComponent } from './modules/trainer/update-profile/update-profile.component';
import { CreateSheduleComponent } from './modules/trainer/create-shedule/create-shedule.component';
import { SheduleTimingsComponent } from './modules/trainer/shedule-timings/shedule-timings.component';
import { ChangePasswordComponent } from './modules/trainer/change-password/change-password.component';
import { ManageFacultyComponent } from './modules/admin/manage-faculty/manage-faculty.component';
import { ManageGymshiftsComponent } from './modules/admin/manage-gymshifts/manage-gymshifts.component';
import { ManageUsersComponent } from './modules/admin/manage-users/manage-users.component';
import { AdminComponent } from './modules/admin/admin.component';
import { CreateAccountComponent } from './components/create-account/create-account.component';
import { LoginComponent } from './components/login/login.component';
import { SectionComponent } from './components/section/section.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreateDietComponent } from './modules/trainer/create-diet/create-diet.component';

const routes: Routes = [
  {path:'',component:SectionComponent},
  {path:'login',component:LoginComponent},
  {path:'register',component:CreateAccountComponent},
  {
    path: 'member',
    loadChildren: () => import('./modules/member/member-routing.module').then(m => m.MemberRoutingModule)
  },
  {path:'admin',component:AdminComponent},
  {path:'manage-users',component:ManageUsersComponent},
  {path:'manage-gym',component:ManageGymshiftsComponent},
  {path:'manage-faculty',component:ManageFacultyComponent},
  {path:'t-pwd',component:ChangePasswordComponent},
  {path:'create-diet',component:CreateDietComponent},
  {path:'create-shedule',component:CreateSheduleComponent},
  {path:'shedule-timings',component:SheduleTimingsComponent},
  {path:'update-profile',component:UpdateProfileComponent},
  {path:'view-profile',component:ViewProfileComponent},
  {path:'trainer',component:TrainerComponent}

  










];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
